import axios from 'axios'

// 创建axios实例
var service = axios.create({
// eslint-disable-next-line
  baseURL: CONFIG_ITEMS.baseURL, // api的base_url
  timeout: 10000, // 请求超过10秒即超时返回错误
})

export default {
  recordData (tid, invite_code) {
    return service.post('/api/account/setInviteCode', null, { params: { tid, invite_code } })
  },
}
