<script>
import axios from '../utils/axios'
import vueQr from 'vue-qr/src/packages/vue-qr.vue'
import Clipboard from 'clipboard'
import imgPC1Url from '../assets/images/PC1_1.svg'
import imgPC2Url from '../assets/images/PC2_1.svg'
import imgPC3Url from '../assets/images/PC3_1.svg'
import imgPC4Url from '../assets/images/PC4_1.svg'
import imgPC5Url from '../assets/images/PC5_1.svg'
import imgM1Url from '../assets/images/M1_1.svg'
import imgM2Url from '../assets/images/M2_1.svg'
import imgM3Url from '../assets/images/M3_1.svg'
import imgM4Url from '../assets/images/M4_1.svg'
import imgM5Url from '../assets/images/M5_1.svg'
import { Autoplay, Pagination, EffectCoverflow } from 'swiper'
import { Swiper, SwiperSlide } from 'swiper/vue'
const modules = [Autoplay, Pagination, EffectCoverflow]
import 'swiper/css';
import 'swiper/css/autoplay'
import 'swiper/css/pagination';
import 'swiper/css/effect-coverflow';
import imgM1UrlForSwiper from '../assets/images/M1_2.svg'
import imgM2UrlForSwiper from '../assets/images/M2_2.svg'
import imgM3UrlForSwiper from '../assets/images/M3_2.svg'
import imgM4UrlForSwiper from '../assets/images/M4_2.svg'
import imgM5UrlForSwiper from '../assets/images/M5_2.svg'

export default {
  data () {
    return {
      viedoUrl: CONFIG_ITEMS.Video_URL,
      qrCodeUrl : CONFIG_ITEMS.qrCodeUrl,
      telegram1Url: CONFIG_ITEMS.Telegram1_URL,
      telegram2Url: CONFIG_ITEMS.Telegram2_URL,
      videoEmail: CONFIG_ITEMS.VideoEmail,
      iosUrl: CONFIG_ITEMS.IOS_URL,
      androidUrl: CONFIG_ITEMS.Android_URL,
      cdnURL: CONFIG_ITEMS.cdnURL,
      inviteCode: '',
      isInstallIos: false,
      isFeedback: false,
      imgPcList: [
        { img: imgPC1Url },
        { img: imgPC2Url },
        { img: imgPC3Url },
        { img: imgPC4Url },
        { img: imgPC5Url },
      ],
      imgList: [
        { img: imgM1Url },
        { img: imgM2Url },
        { img: imgM3Url },
        { img: imgM4Url },
        { img: imgM5Url },
      ],
      swiperList: [
        { img: imgM1UrlForSwiper },
        { img: imgM2UrlForSwiper },
        { img: imgM3UrlForSwiper },
        { img: imgM4UrlForSwiper },
        { img: imgM5UrlForSwiper },
      ],
      swiperAppList: [//
        { img1: '/assets/img/appDownload/M1_1.svg', img2: imgM1UrlForSwiper },
        { img1: '/assets/img/appDownload/M2_1.svg', img2: imgM2UrlForSwiper  },
        { img1: '/assets/img/appDownload/M3_1.svg', img2: imgM3UrlForSwiper  },
        { img1: '/assets/img/appDownload/M4_1.svg', img2: imgM4UrlForSwiper  },
        { img1: '/assets/img/appDownload/M5_1.svg', img2: imgM5UrlForSwiper  },
      ],
      mainImgPc: imgPC1Url,
      mainImg: imgM1Url,
      show: false
    }
  },
  components: {
    Swiper,
    SwiperSlide,
    vueQr
  },
  created () {
    if (this.$route.query.invite_code) {
      this.inviteCode = this.$route.query.invite_code
      this.qrCodeUrl = this.qrCodeUrl + '?invite_code=' + this.inviteCode
    }
    this.recordData()
  },
  methods: {
    onSlideChange (swiper) {
      console.log(swiper.activeIndex)
      const realIndex = (swiper.activeIndex - 1 + this.imgList.length) % (this.imgList.length);
      this.mainImg = this.imgList[realIndex].img
      this.mainImgPc = this.imgPcList[realIndex].img
    },
    downloadApp () {
      if (/iPhone|iPad|iPod/.test(navigator.userAgent)) {
        location.href = this.iosUrl
      }
      else {
        location.href = this.androidUrl
      }
    },
    copy (type) {
      const _this = this
      const clipboardUrl = new Clipboard('#copyUrl')
      const clipboardEmail = new Clipboard('#copyMail')
      if (type === 'copyUrl') {
        clipboardUrl.on('success', function () {
          alert('复制成功')
          clipboardUrl.destroy()
        })
        clipboardUrl.on('error', function () {
          alert('该浏览器不支援自动复制')
          clipboardUrl.destroy()
        })
      }
      else {
        clipboardEmail.on('success', function () {
          alert('复制成功')
          clipboardEmail.destroy()
        })
        clipboardEmail.on('error', function () {
          alert('该浏览器不支援自动复制')
          clipboardEmail.destroy()
        })
      }
    },
    clickHrefBlank (type) {
      window.open(type, '_blank')
    },
    clickHref (type) {
      location.href = type
    },
    downloadImg () {
      const iconUrl = this.$refs['Qrcode'].$el.src
      const a = document.createElement('a')
      const event = new MouseEvent('click')
      a.download = 'QRCODE'
      a.href = iconUrl
      a.dispatchEvent(event)
    },
    async recordData () {
      await axios.recordData(10001, this.inviteCode
      ).then(res => {
        console.log(res)
      })
    },
    installIos () {
      window.scrollTo(0, 0)
      this.isInstallIos = true
    },
    feedback () {
      window.scrollTo(0, 0)
      this.isFeedback = true
    },
    back () {
      this.isInstallIos = false
      this.isFeedback = false
    },
    onInputChange () {
      document.getElementById('countId').innerHTML = myTextarea.describe.value.length + '/200'
    },
    onSubmit () {
      let titleType = formRadio.applicationSystem.value //1。无法下载  2。下载后无法进入 3 。其他
      let description = myTextarea.describe.value  //反馈的描述内容
      if (!titleType) {
        return alert('请选择分类～')
      }
      if (!description) {
        return alert('请填写反馈内容～')
      }
      document.getElementById("check1").checked = false
      document.getElementById("check2").checked = false
      document.getElementById("check3").checked = false
      document.getElementById('textareaId').value = ''
      this.isFeedback = false
      alert('反馈成功,感谢您的支持')
    },
    loadImageShow () {
      this.show = true
    }
  }
}
</script>
